import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
import Email from "@material-ui/icons/Email";
import AccountCircle from "@material-ui/icons/AccountCircle";
import People from "@material-ui/icons/People";
import Header from "../customComponents/Header";
import HeaderLinks from "../customComponents/HeaderLinks";
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import PersonOutlineOutlinedIcon from '@material-ui/icons/PersonOutlineOutlined';
import { Link, useHistory } from "react-router-dom";

// import CardHeader from '@material-ui/core/CardHeader';
// import Card from "@material-ui/core/Card";
// import CardBody from "@material-ui/core/CardContent";
// import CardFooter from "@material-ui/core/CardActions";
// import CustomInput from "components/CustomInput/CustomInput.js";
import Card from "../customComponents/Card";
import CardBody from "../customComponents/CardBody.js";
import CardHeader from "../customComponents/CardHeader.js";
import CardFooter from "../customComponents/CardFooter.js";
// import Footer from "components/Footer/Footer.js";
// import GridContainer from "components/Grid/GridContainer.js";
// import GridItem from "components/Grid/GridItem.js";
// import Button from "components/CustomButtons/Button.js";
import TextField from "@material-ui/core/TextField";
import styles from "../customStyle/loginPage";

import image from "./landing-bg2.jpg";

const useStyles = makeStyles(styles);

export default function LoginPage(props) {
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  setTimeout(function () {
    setCardAnimation("");
  }, 700);
  const classes = useStyles();
  const { ...rest } = props;
  const history = useHistory();
  const handleClick = (loc) => (e) => {
    e.preventDefault();
    history.push(loc);
  }
  return (
    <div>
      <Header
        color="transparent"
        // routes={dashboardRoutes}
        brand="EyeSpy"
        rightLinks={<HeaderLinks />}
        absolute
        changeColorOnScroll={{
          height: 400,
          color: "white"
        }}
        {...rest}
      />
      <div
        className={classes.pageHeader}
        style={{
          backgroundImage: "url(" + image + ")",
          backgroundSize: "cover",
          backgroundPosition: "top center"
        }}
      >
        <div className={classes.container}>
          <Grid container justify="center" className={classes.styleGridContainer}>
            <Grid item xs={12} sm={12} md={4} className={classes.styleGridItem}>
              <Card className={classes[cardAnimaton]}>
                <form className={classes.form}>
                  <CardHeader color="primary" className={classes.cardHeader} style={{
                    background: "linear-gradient(60deg, #00acc1, #4db6ac)",
                    boxShadow: "0 12px 20px -10px #00acc14a, 0 4px 20px 0px #61dafb1f, 0 7px 8px -5px #00acc117"
                  }}>
                    <h4 style={{ fontSize: "1.225rem", lineHeight: "1.5em", fontFamily: "Roboto, Helvetica, Arial, sans-serif", fontWeight: "400" }}>Login</h4>
                  </CardHeader>
                  <p className={classes.divider} style={{ fontFamily: "Roboto" }}>Enter your details</p>
                  <CardBody>
                    <TextField
                      className={classes.margin}
                      id="input-with-icon-textfield"
                      label="Username"
                      style={{margin: "27px 0 17px 0",
                        width: "100%"}}
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <PersonOutlineOutlinedIcon />
                          </InputAdornment>
                        ),
                      }}
                    />
                    <TextField
                      className={classes.margin}
                      id="input-with-icon-password"
                      label="Password"
                      type="password"
                      style={{margin: "20px 0 17px 0",
                        width: "100%"}}
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <LockOutlinedIcon />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </CardBody>
                  <CardFooter className={classes.cardFooter}>
                    <Button simple color="primary" size="lg" onClick={handleClick(`/admin/dashboard`)}>
                      Get started
                    </Button>
                  </CardFooter>
                </form>
              </Card>
            </Grid>
          </Grid>
        </div>
        {/* <Footer whiteFont /> */}
      </div>
    </div>
  );
}
