import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Fab from '@material-ui/core/Fab';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import Grid from '@material-ui/core/Grid';
import MenuItem from '@material-ui/core/MenuItem';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';

const useStyles = makeStyles({
    AddProjectButton: {
        right: '15px',
        bottom: '68px',
        position: 'fixed',
        zIndex: "3",
    },
    sideMargin:{
        marginRight:'20px'
    },
    bottomMargin:{
        marginBottom:'20px'
    },
    arCheck:{
      margin:'0 16px 0 0'
    },
    productDesc:{
        height:'100px'
    },
    btnGrid: {
      position:'absolute',
      bottom: '25px',
      right:'20px'
    }
});

export default function AddProductModal() {
  
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [addProductJson,setFormData] = React.useState({
    productName:"",
    productCategory:"",
    stories :0,
    price :0,
    stock:0,
    arModel:"",
    imgSrc:"",
    productDesc:"",
    shippingCharges:0,
    arEnabled:false,

  })
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  function handleChange(evt) {
    const value =
      evt.target.type === "checkbox" ? evt.target.checked : evt.target.value;
      setFormData({
      ...addProductJson,
      [evt.target.name]: value
    });
  }

  const handleFormSubmit = () => {
    setOpen(false);
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(addProductJson)
  };
  fetch('/registerProduct/', requestOptions).then(function(res){
        return res.json();
      });

  }
  return (
    <div>
      <Fab size="medium" color="secondary" aria-label="add Product" className={classes.AddProjectButton} onClick={handleClickOpen}>
        <AddCircleIcon />
      </Fab>
      <Dialog fullScreen open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Add Product</DialogTitle>
        <DialogContent>
            <form noValidate autoComplete="off" >
                <TextField className ={classes.bottomMargin}
                autoFocus
                margin="dense"
                label="Product Name"
                name="productName"
                fullWidth color="secondary" value={addProductJson.productName} onChange={handleChange}/>
                <Grid className ={classes.bottomMargin} container>
                <Grid item sm={5} xs={5} className ={classes.sideMargin}> 
                    <TextField
                    margin="dense"
                    name="productCategory"
                    label="Category" color="secondary" value={addProductJson.productCategory} onChange={handleChange}/>
                </Grid>
                <Grid item sm={3} xs={3}>
                    <TextField
                    margin="dense"
                    name="stories"
                    label="Stories" variant="outlined" type="number" color="secondary" onChange={handleChange} value={addProductJson.stories}/>
                </Grid>
                </Grid>
                
                <Grid className ={classes.bottomMargin} container>
                    <Grid item sm={3} xs={3} className ={classes.sideMargin}>
                        <TextField
                        margin="dense"
                        name="price"
                        label="Price" value={addProductJson.price} onChange={handleChange} variant="outlined" type="number" color="secondary" >
                            <MenuItem key="INR" value="₹"></MenuItem>
                        </TextField>
                    </Grid>
                    <Grid item sm={3} xs={3} className ={classes.sideMargin}>
                        <TextField
                        margin="dense"
                        name="stock"
                        label="Stock" variant="outlined" type="number" color="secondary" onChange={handleChange} value={addProductJson.stock}/>
                    </Grid>
                    <Grid item sm={3} xs={3}>
                        <TextField
                        margin="dense"
                        name="shippingCharges"
                        label="Shipping" variant="outlined" type="number" onChange={handleChange} value={addProductJson.shippingCharges} color="secondary" />
                    </Grid>
                    </Grid>
                    <Grid container>
                          
                    <FormControlLabel  className={classes.arCheck} control={<Checkbox color="secondary" name="arEnabled" onChange={handleChange} checked={addProductJson.arEnabled} />} label="AR Enabled" labelPlacement="start"/>
                        <Grid item xs={11} sm={11}>
                        <TextField className ={classes.bottomMargin}
                        margin="dense"
                        name="arModel"
                        label="AR Model url"
                        color="secondary"  onChange={handleChange} value={addProductJson.arModel} fullWidth/>
                        </Grid>
                    </Grid>
                    
                    <Grid item xs={11} sm={11}>
                    <TextField className ={classes.bottomMargin}
                        margin="dense"
                        name="imgSrc"
                        label="Image url"
                        fullWidth color="secondary" onChange={handleChange} value={addProductJson.imgSrc}/>
                    </Grid>
                    <TextField
                        margin="dense"
                        name="productDesc"
                        label="product description"
                        fullWidth color="secondary" onChange={handleChange} variant="outlined" multiline={true} value={addProductJson.productDesc}/>
                    <Grid>

                    </Grid>
                    </form>
                    <Grid className={classes.btnGrid}>
                          <Button onClick={handleClose} color="secondary">
                          CANCEL
                        </Button>
                        <Button type="submit" onClick={handleFormSubmit} color="secondary">
                          ADD
                        </Button>
                    </Grid>
                </DialogContent>
      </Dialog>
    </div>
  );
}