import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Fab from '@material-ui/core/Fab';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import MenuItem from '@material-ui/core/MenuItem';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import PropTypes from "prop-types";
import DialogActions from '@material-ui/core/DialogActions';


const EditProductModal = ({ open, close, formData, handleChange, onUpdate }) => (
    <Dialog fullScreen open={open} onClose={close} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Edit Product</DialogTitle>
        <DialogContent>
            <TextField autoFocus margin="dense" label="Product Name" name="productName" fullWidth color="secondary" 
            defaultValue={formData != undefined ? formData.properties.productName : ''} onChange={handleChange} />
            <Grid item sm={5} xs={5} >
                <TextField margin="dense" name="productCategory" label="Category" color="secondary" 
                    defaultValue={formData != undefined ? formData.properties.productCategory:''} onChange={handleChange} />
            </Grid>
            <Grid item sm={3} xs={3}>
                <TextField margin="dense" name="stories" label="Stories" variant="outlined" type="number" color="secondary" 
                onChange={handleChange} defaultValue={formData != undefined ? formData.properties.stories:''} />
            </Grid>
        <Grid container>
            <Grid item sm={3} xs={3} style={{marginRight:"10px"}} >
                <TextField margin="dense" name="price" label="Price" 
                defaultValue={formData != undefined ? formData.properties.price:0} onChange={handleChange} variant="outlined" type="number" color="secondary" >
                <MenuItem key="INR" value="₹"></MenuItem>
                </TextField>
            </Grid>
            <Grid item sm={3} xs={3} style={{marginRight:"10px"}}>
                <TextField margin="dense" name="stock" label="Stock" variant="outlined" type="number" color="secondary" 
                onChange={handleChange} defaultValue={formData != undefined ? formData.properties.stock:0} />
            </Grid>
            <Grid item sm={3} xs={3}>
                <TextField margin="dense" name="shippingCharges" label="Shipping" variant="outlined" type="number" 
                onChange={handleChange} defaultValue={formData != undefined ? formData.properties.shippingCharges:0} color="secondary" />
            </Grid>
        </Grid>
        <Grid container>
            <FormControlLabel control={<Checkbox color="secondary" name="arEnabled" 
            onChange={handleChange} checked={formData != undefined ? formData.properties.arEnabled : false} />} label="AR Enabled" labelPlacement="start" />
            <Grid item xs={11} sm={11}>
                <TextField margin="dense" name="arModel" label="AR Model url" color="secondary" 
                onChange={handleChange} defaultValue={formData != undefined ? formData.properties.arModel.replace(/[\[\]]/g, '').split(",")[0]:''} fullWidth />
            </Grid>
        </Grid>

        <Grid item xs={11} sm={11}>
            <TextField margin="dense" name="imgSrc" label="Image url" fullWidth color="secondary" 
            onChange={handleChange} defaultValue={formData != undefined ? formData.properties.imgSrc.replace(/[\[\]]/g, '').split(",")[0]:false} />
        </Grid>
        <TextField margin="dense" name="productDesc" label="product description" fullWidth color="secondary" 
        onChange={handleChange} variant="outlined" multiline={true} defaultValue={formData != undefined ? formData.properties.productDesc:''} />
         </DialogContent>
    <DialogActions>
        <Button onClick={close} color="secondary">
            Cancel
          </Button>
        <Button onClick={onUpdate} color="secondary">
            Update
          </Button>
    </DialogActions>
    </Dialog >
)


EditProductModal.propTypes = {
    close: PropTypes.func.isRequired,
    open: PropTypes.bool.isRequired,
    formData: PropTypes.object,
    handleChange: PropTypes.func,
    onUpdate: PropTypes.func,
};

export default EditProductModal;