import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import './App.css';

import { ThemeProvider as MuiThemeProvider } from '@material-ui/core/styles';
import Theme from "./components/admin/theme";
import ScrollTop from "./components/admin/ScrollTop";
import AdminDashboard from "./components/admin/AdminDashboard";
import AdminCatalog from "./components/admin/AdminCatalog";
import AdminStatistics from "./components/admin/AdminStatistics";
import AdminFooter from "./components/admin/AdminFooter";
import AdminHeader from "./components/admin/AdminHeader";
import AdminLogin from "./components/admin/LoginPage";
// import AdminLogin from "./components/admin/AdminLogin";
// import Homepage from "./new/views/LandingPage/LandingPage";
// import Homepage from "./components/admin/AdminDashboard";
import Homepage from "./components/admin/Homepage";

const PrivateRoute = ({ component: Component, ...rest }) => {
  console.log("Component", Component)
  console.log("rest.user", rest.user)
  console.log("rest", rest)
  return (
    <Route
      {...rest}
      render={(props) =>
        rest.user ?
          rest.user.email == 'admin' ? (
            <Component {...props} />
          )
            :
            (
              <Redirect to="/login" />
            )
          : null
      }
    />
  );
}

function App() {
  const [user, setUser] = useState(null);
  const [token, setToken] = useState("");

  const authenticate = (user, token) => {
    console.log('APP.js user', user)
    setUser(user);
    setToken(token);
    window.localStorage.setItem('user', JSON.stringify(user));
    window.localStorage.setItem('token', JSON.stringify(token));
  }

  const signout = () => {
    setUser({ email: "anonymous" });
    setToken(null);
    window.localStorage.removeItem('user');
    window.localStorage.removeItem('token');
  }

  useEffect(() => {
    const jsonUser = window.localStorage.getItem('user')
    const jsonToken = window.localStorage.getItem('token')
    if (jsonUser && jsonToken) {
      const user = JSON.parse(jsonUser);
      console.log(user)
      const token = jsonToken
      setUser(user);
      setToken(token);
    }
    else setUser({ email: "anonymous" })
  }, [])

  return (
    <Router>
      <ScrollTop />
      <MuiThemeProvider theme={Theme}>
        <div className="App">
        {/* <AdminHeader /> */}
          <Switch>
            <Route strict path="/admin/" >
              <AdminHeader />
            </Route>
            {/* <Route>
              <Navbar user={user} signout={signout} />
            </Route> */}
          </Switch>
          <Route exact path="/" component={Homepage}/>
          <Route exact path="/login">
            <AdminLogin authenticate={authenticate} />
          </Route>
          <Route exact path="/admin/stats" component={AdminStatistics} user={user} />
          <Route exact path="/admin/catalog" component={AdminCatalog} user={user} />
          <Route exact path="/admin/dashboard" component={AdminDashboard} user={user} />
          {/* <PrivateRoute exact path="/admin/stats" component={AdminStatistics} user={user} />
          <PrivateRoute exact path="/admin/catalog" component={AdminCatalog} user={user} />
          <PrivateRoute exact path="/admin/dashboard" component={AdminDashboard} user={user} /> */}
          <AdminFooter />
          {/* <Switch>
            <Route strict path="/admin/" >
              
            </Route>
          </Switch> */}
        </div>
      </MuiThemeProvider>
    </Router>
  );
}

export default App;
