import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import PropTypes from "prop-types";

const DeleteProductModal = ({openModal,onCloseModal,onDelete})=>(
      <Dialog open={openModal} onClose={onCloseModal} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Delete Product</DialogTitle>
        <DialogContent>
          <DialogContentText>
                Are you sure to delete this Product?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={onCloseModal} color="secondary">
            Cancel
          </Button>
          <Button onClick={onDelete} color="secondary">
            Delete
          </Button>
        </DialogActions>
      </Dialog>
  )

DeleteProductModal.propTypes = {
    onCloseModal: PropTypes.func.isRequired,
    openModal: PropTypes.bool.isRequired,
    onDelete: PropTypes.func.isRequired,
};
  
export default DeleteProductModal;