import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from '@material-ui/core/Grid';
// import GridContainer from "components/Grid/GridContainer.js";
// import GridItem from "components/Grid/GridItem.js";
// import CustomInput from "components/CustomInput/CustomInput.js";
import CustomInput from "@material-ui/core/TextField";
// import Button from "components/CustomButtons/Button.js";
import Button from "@material-ui/core/Button";
import SendIcon from '@material-ui/icons/Send';
import SuccessModal from "./SuccessModal";

import styles from "../customStyle/workStyle";

const useStyles = makeStyles(styles);

export default function WorkSection() {
  const classes = useStyles();
  return (
    <div className={classes.section}>
      <Grid container justify="center">
        <Grid item cs={12} sm={12} md={8}>
          <h2 className={classes.title}>Get in Touch!</h2>
          <h4 className={classes.description}>
            Drop a note to us with your requirement and we will get back to you shortly.
          </h4>
          <form>
            <Grid container className={classes.styleGridContainer}>
              <Grid item xs={12} sm={12} md={6} className={classes.styleGridItem}>
                <CustomInput
                  id="standard-full-width"
                  label="Your Name"
                  style={{ margin: 0 }}
                  placeholder=""
                  fullWidth
                  margin="normal"
                  InputLabelProps={{

                  }}
                />
              </Grid>
              <Grid item xs={12} sm={12} md={6} className={classes.styleGridItem}>
                <CustomInput
                  id="standard-full-width"
                  label="Your Email"
                  style={{ margin: 0 }}
                  placeholder=""
                  fullWidth
                  margin="normal"
                  InputLabelProps={{
                  }}
                />
              </Grid>
              <CustomInput
                id="standard-full-width"
                label="Your Message"
                style={{ marginLeft: "15px", marginRight: "15px" }}
                placeholder=""
                fullWidth
                margin="normal"
                multiline
                rows={4}
                color="primary"
              />
              <Grid container justify="center">
                <Grid item xs={12} sm={12} md={4} className={classes.textCenter}>
                  {/* <Button
                    variant="contained"
                    className={classes.button}
                    endIcon={<SendIcon />}
                    color="primary"
                  >
                    Send Enquiry
      </Button> */}
                  <SuccessModal />
                </Grid>
              </Grid>
            </Grid>
          </form>
        </Grid>
      </Grid>
    </div>
  );
}
