import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SendIcon from '@material-ui/icons/Send';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';


function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  modalStyle: {
    position: 'absolute',
    width: "95%",
    maxWidth:"500px",
    backgroundColor: theme.palette.background.paper,
    // border: '2px solid #000',
    boxShadow: theme.shadows[5],
    borderRadius: "6px",
    // padding: theme.spacing(2, 4, 3),
    '&:focus': {
        outline: 'none',
      },
      '& :focus': {
        outline: 'none',
      }
  },
  modalMessage:{
    color: "#3C4858",
    margin: "1.75rem 0 0.875rem",
    fontSize: "1.25rem",
    marginTop: "20px",
    minHeight: "32px",
    textAlign: "center",
    fontFamily: "Roboto Slab, Times New Roman, serif",
    fontWeight: "700",
    lineHeight: "1.5em",
    marginBottom: "30px",
    textDecoration: "none",
    marginLeft: "30px",
    marginRight: "30px",
  },
  iconStyle:{
    margin:"auto",
    display:"block",
    fontSize:"3.5em",
    marginTop:"30px"
  }
  
}));

export default function VideoModal() {
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const body = (
    <div style={modalStyle} className={classes.modalStyle}>
      {/* <video controls style={{width:"100%", maxHeight:"500px", display:"block"}}>
          <source src="./images/ElevatorPitch.mp4">
        </source>
      </video> */}
      <CheckCircleIcon className={classes.iconStyle} color="primary"/>
      <h1 className={classes.modalMessage}>Your Enquiry has been sent! Expect a Reply Soon! Looking Forward to working with you!</h1>
    </div>
  );

  return (
    <div>
      {/* <button type="button" onClick={handleOpen}>
        Open Modal
      </button> */}
      <Button
                    variant="contained"
                    className={classes.button}
                    endIcon={<SendIcon />}
                    color="primary"
                    onClick={handleOpen}
                  >
                    Send Enquiry
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
}