import { title } from "./material-kit-react";

const productStyle = {
  section: {
    padding: "70px 0",
    textAlign: "center"
  },
  title: {
    ...title,
    marginBottom: "1rem",
    marginTop: "30px",
    minHeight: "32px",
    textDecoration: "none"
  },
  description: {
    color: "#999",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    fontWeight: 300,
    marginTop: "10px",
    marginBottom: "10px"
  },
  styleGridItem:{
    width: "100%",
    position: "relative",
    flexBasis: "auto",
    minHeight: "1px",
    paddingLeft: "15px",
    paddingRight: "15px"
  },
  styleGridContainer:{
    width: "auto",
    marginLeft: "-15px",
    marginRight: "-15px"
  }
};

export default productStyle;
