import React, { PureComponent } from 'react';
import {
    AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Label,PieChart, Pie, Sector
} from 'recharts';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import CardContent from '@material-ui/core/CardContent';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PeopleIcon from '@material-ui/icons/People';

const data = [
    {
        name: 'Oct', hits: 3000,
    },
    {
        name: 'Nov', hits: 2000,
    },
    {
        name: 'Dec', hits: 2780,
    },
    {
        name: 'Jan', hits: 4890,
    },
    {
        name: 'Feb', hits: 2390,
    },
    {
        name: 'Mar', hits: 3490,
    },
];
const categoryData = [
    { name: 'Apparel', value: 400 },
    { name: 'Shoes', value: 300 },
    { name: 'Bicycles', value: 350 },
    { name: 'Bottles', value: 150 },
  ];

const useStyles = theme => ({
    root:{
      border: '1px solid #babdc2',
      margin: '10px',
      background : '#24406e',
      color: '#fff'
    },
    Wrapper :{
        padding: "20px",
        margin: 'auto',
        background: '#f0f3f7'
    },
    centerAlign :{
        margin: '0 auto',
        color: '#000',
        fontWeight:'bold'
    },
    statContent: {
      display :'flex',
      alignItems : 'center',
      justifyContent:'center',
      paddingBottom: '16px !important'
    },
    statIcon : {
      margin :'4px 7px 0 0'
    },
    statDesc : {
      justifyContent : 'center'
    },
    statNum : {
      marginLeft: '7px',
      fontSize : '24px',
      fontWeight: 'bold'
    },
    fullWidthCard : {
      width: '100%',
      background : '#fff'
    },
    kpiHeading :{
      width:'100%',
      fontSize:'15px',
      textAlign:"left",
      borderBottom :"1px solid #444",
      marginBottom:"20px"
    },
    kpiImage :{
      width:"120px",
      height:"120px",
      flexBasis:"35%"
    },
    kpiStat : {
      fontSize:"24px",
      fontWeight:"bold",
      display:"inline-block",
      marginRight:"8px"
    },
    kpiContent : {
      marginBottom: "20px",
      display:"flex",
      alignItems:"center",
      justifyContent:"center"
    },
    kpiDesc: {
      flexBasis:"65%",
      flexDirection:"column",
    },
    kpiWrapper:{
      justifyContent:"center"
    }
  });

const statCardData =[
  {
      icon : <CalendarTodayIcon/>,
      text : "Sales Monthly - ",
      statnum : '$30,000'
  },
  {
    icon : <MonetizationOnIcon/>,
    text : "Sales Annual - ",
    statnum : "$230,000"
  },
  {
    icon : <ShoppingCartIcon/>,
    text : "Orders to be delivered - ",
    statnum : "356"
  },
  {
    icon : <PeopleIcon/>,
    text : "New subscriptions - ",
    statnum : "433"
  }
]  

const kpiData =[
  {
    heading:"Customers Using AR",
    kpiImg:"https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSuGfgbOH2v-pfXWKzLw8oAi5AGQlg-8r4bJJY2DDWfjfOgGL1e&usqp=CAU",
    statPercent:"32%",
    statDescription:"usage rate past 30 days"
  },
  {
    heading:"Conversion Rate",
    kpiImg:"data:image/jpeg;base64,",
    statPercent:"17%",
    statDescription:"for the past 30 days"
  },
  {
    heading:"Cart Abandoment Rate",
    kpiImg:"https://lh3.googleusercontent.com/proxy/R_p2IHJKXFrQSFgjMcnUbd4R8dIrZ15VyB9Nd5h-Ka8OiD-ZJ4ty32G6TD7t6Iatkp5dZP7moGSVWI1pH0HPXevX5lMFL3OQ9-TdEpWJGdJ7GbwO",
    statPercent:"39%",
    statDescription:"for the past 30 days"
  },
  {
    heading:"Order Value",
    kpiImg:"data:image/png;base64,",
    statPercent:"$160",
    statDescription:"Average Order Value"
  },
  {
    heading:"Visits per Purchase",
    kpiImg:"https://cdn2.iconfinder.com/data/icons/shopping-e-commerce-3/512/laptop-store-512.png",
    statPercent:"3",
    statDescription:"visits before a purchase is made"
  },
  {
    heading:"Late Delivery",
    kpiImg:"https://cdn4.iconfinder.com/data/icons/logistic-filled-outline-1/32/logistic-shipping-shoping-global_41-2-512.png",
    statPercent:"5",
    statDescription:"orders delivered late this week"
  }]
const renderActiveShape = (props) => {
  const RADIAN = Math.PI / 180;
  const {
    cx, cy, midAngle, innerRadius, outerRadius, startAngle, endAngle,
    fill, payload, percent, value,
  } = props;
  const sin = Math.sin(-RADIAN * midAngle);
  const cos = Math.cos(-RADIAN * midAngle);
  const sx = cx + (outerRadius + 10) * cos;
  const sy = cy + (outerRadius + 10) * sin;
  const mx = cx + (outerRadius + 30) * cos;
  const my = cy + (outerRadius + 30) * sin;
  const ex = mx + (cos >= 0 ? 1 : -1) * 22;
  const ey = my;
  const textAnchor = cos >= 0 ? 'start' : 'end';

  return (
    <g>
      <text x={cx} y={cy} dy={8} textAnchor="middle" fill={fill}>{payload.name}</text>
      <Sector
        cx={cx}
        cy={cy}
        innerRadius={innerRadius}
        outerRadius={outerRadius}
        startAngle={startAngle}
        endAngle={endAngle}
        fill={fill}
      />
      <Sector
        cx={cx}
        cy={cy}
        startAngle={startAngle}
        endAngle={endAngle}
        innerRadius={outerRadius + 6}
        outerRadius={outerRadius + 10}
        fill={fill}
      />
      <path d={`M${sx},${sy}L${mx},${my}L${ex},${ey}`} stroke={fill} fill="none" />
      <circle cx={ex} cy={ey} r={2} fill={fill} stroke="none" />
      <text x={ex + (cos >= 0 ? 1 : -1) * 12} y={ey} textAnchor={textAnchor} fill="#333">{`${value} units sold`}</text>
      <text x={ex + (cos >= 0 ? 1 : -1) * 12} y={ey} dy={18} textAnchor={textAnchor} fill="#999">
        {`(Rate ${(percent * 100).toFixed(2)}%)`}
      </text>
    </g>
  );
};

  
class statComponent extends PureComponent{
    state = {
        activeIndex: 0,
      };
    
      onPieEnter = (data, index) => {
        this.setState({
          activeIndex: index,
        });
      };
    render() {
          const { classes } = this.props;
          return (
                <Grid container spacing={3} className={classes.Wrapper}>
                    <Grid container item xs={12} sm={12}>
                      {statCardData.map(stat =>(
                        <Grid container item xs={12} sm={3}>
                        <Card className={classes.root} style={{width:"100%"}} variant="outlined">
                          <CardContent className={classes.statContent}>
                            <div  className={classes.statIcon}>{stat.icon}</div>
                            <div  className={classes.statDesc}>{stat.text}</div>
                            <div className={classes.statNum}>{stat.statnum}</div>
                          </CardContent>
                        </Card>
                        </Grid>
                      ))}
                      </Grid>             
                             <Grid container spacing={2} item xs={12} sm={12}> 
                    <Grid container item sm={6} xs={12} >
                    <Card className={classes.fullWidthCard}>
                      <CardContent>
                          <span className={classes.centerAlign}>Website Traffic</span>
                            <div style={{ width: '100%', height: 400 }}>
                            <ResponsiveContainer>
                                <AreaChart data={data}
                                        margin={{
                                            top: 10, right: 30, left: 0, bottom: 0,
                                        }}>
                                    <defs>
                                      <linearGradient id="colorPv" x1="0" y1="0" x2="0" y2="1">
                                          <stop offset="5%" stopColor="#eb8e15" stopOpacity={0.8} />
                                          <stop offset="95%" stopColor="#eb8e15" stopOpacity={0} />
                                      </linearGradient>
                                  </defs>
                                  <CartesianGrid strokeDasharray="3 3" />
                                  <XAxis dataKey="name" ><Label  position="insideBottomRight" >Months</Label></XAxis>
                                  <YAxis> <Label position="insideTopLeft" >No of hits</Label></YAxis>
                                  
                                  <Tooltip />
                                  <Area type="monotone" dataKey="hits" stroke="#eb8e15" fillOpacity={1} fill="url(#colorPv)" />
                              </AreaChart>
                                </ResponsiveContainer>
                            </div>
                      </CardContent>
                    </Card>

                    </Grid>
                    <Grid container item sm={6} xs={12}>
                      <Card className={classes.fullWidthCard}>
                        <CardContent>
                        <span className={classes.centerAlign}>Most Popular Categories</span> 
                        <div style={{ width: '100%', height: 400 }}>
                          <ResponsiveContainer >
                            <PieChart className={classes.centerAlign}  >
                                <Pie  activeIndex={this.state.activeIndex} activeShape={renderActiveShape} data={categoryData}
                                cx="50%" cy="50%" innerRadius={50} outerRadius={70} fill="#09756e" dataKey="value" onMouseEnter={this.onPieEnter} />
                            </PieChart>
                            </ResponsiveContainer>
                        </div>
                        
                        </CardContent>
                      </Card>
                    </Grid>
                    


                   
                      
                    </Grid>
                    <Grid item xs={12} sm={12}>
                      <h2>Purchase Insights</h2>
                    </Grid>
                    <Grid container item xs={12} sm={12} className={classes.kpiWrapper}>
                    {kpiData.map(kpi =>(
                      <Grid item xs={12} sm={3} style={{margin:"10px"}}>
                      <Card>
                        <CardContent>
                    <div className={classes.kpiHeading}>{kpi.heading}</div>
                          <div>
                            <div className={classes.kpiContent}>
                              <img className={classes.kpiImage} src={kpi.kpiImg}/>
                              <div className={classes.kpiDesc}>
                                  <div  className={classes.kpiStat}>{kpi.statPercent}</div>
                                  <div>{kpi.statDescription}</div>
                              </div>
                            </div>
                            
                          </div>
                        </CardContent>
                      </Card>
                    </Grid>
                    ))}
                      
                    </Grid>
                    
                </Grid>

        );
                            }
}

export default withStyles(useStyles)(statComponent)