export default function TeamSection() {
  const classes = useStyles();
  const imageClasses = classNames(
    classes.imgRaised,
    classes.imgRoundedCircle,
    classes.imgFluid
  );
  return (
    <div className={classes.section}>
      <h2 className={classes.title} style={{fontSize: "2.25rem", lineHeight: "1.5em"}}>Here is our team</h2>
      <div>
        <Grid container className={classes.styleGridContainer}>
          {teamMember.map(member=>(
          <Grid item xs={12} sm={12} md={3} className={classes.styleGridItem}>
            <Card className={classes.cardStyle}>
              <Grid item xs={12} sm={12} md={6} className={classes.itemGrid}>
                <img src={member.imgPath} alt="..." className={imageClasses} />
              </Grid>
              <h4 className={classes.cardTitle}>
                {member.name}
                <br />
                <small className={classes.smallTitle}>{member.title}</small>
              </h4>
              {/* <CardBody className={classes.cardContent}>
                <p className={classes.description}>
                  {member.description}
                </p>
              </CardBody> */}
            </Card>
          </Grid>
          ))}
        </Grid>
      </div>
    </div>
  );
}
