import { cardTitle, title } from "./material-kit-react";
import imagesStyle from "./imagesStyles";

const teamStyle = {
  section: {
    padding: "70px 0",
    textAlign: "center"
  },
  title: {
    ...title,
    marginBottom: "1rem",
    marginTop: "30px",
    minHeight: "32px",
    textDecoration: "none"
  },
  ...imagesStyle,
  itemGrid: {
    marginLeft: "auto",
    marginRight: "auto",
    paddingLeft: "15px",
    paddingRight: "15px"
  },
  cardTitle,
  smallTitle: {
    color: "#6c757d",
  },
  description: {
    color: "#999",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    fontWeight: 300,
    marginTop: "10px",
    marginBottom: "10px",
    lineHeight: "1.5"
  },
  justifyCenter: {
    justifyContent: "center !important",
    padding:"0.9375rem 1.875rem"
  },
  socials: {
    marginTop: "0",
    width: "100%",
    transform: "none",
    left: "0",
    top: "0",
    height: "100%",
    lineHeight: "41px",
    fontSize: "20px",
    color: "#999"
  },
  margin5: {
    margin: "5px"
  },
  styleGridItem:{
    width: "100%",
    position: "relative",
    flexBasis: "auto",
    minHeight: "1px",
    paddingLeft: "15px",
    paddingRight: "15px"
  },
  styleGridContainer:{
    width: "auto",
    marginLeft: "-15px",
    marginRight: "-15px"
  },
  cardStyle:{
    boxShadow: "none",
    color: "rgba(0, 0, 0, 0.87)",
    width: "100%",
    border: "0",
    display: "flex",
    position: "relative",
    fontSize: ".875rem",
    minWidth: "0",
    wordWrap: "break-word",
    marginTop: "30px",
    transition: "all 300ms linear",
    borderRadius: "6px",
    marginBottom: "30px",
    flexDirection: "column",
  },
  cardContent:{
    padding: "0.9375rem 1.875rem"
  }
};

export default teamStyle;
