import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Chat from "@material-ui/icons/Chat";
import ImageSearchIcon from '@material-ui/icons/ImageSearch'
import VerifiedUser from "@material-ui/icons/VerifiedUser";
import LocalMallIcon from '@material-ui/icons/LocalMall';
import Fingerprint from "@material-ui/icons/Fingerprint";
import AR from "./images/ar"
import Recommendations from "./images/recommendation";
import Vs from "./images/visualSearch"
// import GridContainer from "components/Grid/GridContainer.js";
// import GridItem from "components/Grid/GridItem.js";
import InfoArea from "./InfoArea";
import Grid from '@material-ui/core/Grid';

import styles from "../customStyle/productStyle";

const useStyles = makeStyles(styles);

export default function ProductSection() {
  const classes = useStyles();
  return (
    <div className={classes.section}>
      <Grid container justify="center" className={classes.styleGridContainer}>
        <Grid item xs={12} sm={12} md={8} className={classes.styleGridItem}>
          <h2 className={classes.title} style={{fontSize: "2.25rem", lineHeight: "1.5em"}}>
            What we offer - Our Product</h2>
          <h5 className={classes.description} style={{fontSize: "1.0625rem", lineHeight: "1.55em"}}>
          EyeSpy is a one-stop solution built by our team, Code Zeppelin, 
          which provides an opportunity to retailers for enhancing their customer's shopping experience from Product Discovery to Conversion. Find the answer to most common dilemma's in a shoppers' mind here.
          All the three features of EyeSpy are mutually exclusive and can be leveraged individually based on your requirement.
          </h5>
        </Grid>
      </Grid>
      <div>
        <Grid container className={classes.styleGridContainer}>
          <Grid item xs={12} sm={12} md={4} className={classes.styleGridItem}>
            <InfoArea
              title="Visual Search"
              description="Tap into the new Sensory Search - Visual Search and enable your customers to discover the products they Need & Desire."
              icon={Vs}
              iconColor="info"
              vertical
            />
          </Grid>
          <Grid item xs={12} sm={12} md={4} className={classes.styleGridItem}>
            <InfoArea
              title="Personalized Recommendations"
              description="Provide Personalized Search Results & Recommendations with our ML based algorithms based on user interests & interactions."
              icon={Recommendations}
              iconColor="success"
              vertical
            />
          </Grid>
          <Grid item xs={12} sm={12} md={4} className={classes.styleGridItem}>
            <InfoArea
              title="Augmented Reality"
              description="Solve your customers' problems by providing a 3D view of the product & enable them to Experience the product in Reality with AR."
              icon={AR}
              iconColor="danger"
              vertical
            />
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
