import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import SearchIcon from '@material-ui/icons/Search';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import AddProductModal from "./AddProductModal";
import DeleteProductModal from "./DeleteProductModal"
import EditProductModal from "./EditProductModal"

const useStyles = makeStyles({
  catalogWrapper: {
    padding: "20px",
    background: '#f0f3f7'
  },
  root: {
    maxWidth: 250,
    background: "#0d072b",
    borderColor: "#000000",
    padding: "10px",
    margin: '0 auto',
    borderRadius: "15px",
    height: '100%',
    position:"relative"
  },
  media: {
    height: 140,
    backgroundSize: 'contain',
    backgroundColor: '#fff'
  },
  productName: {
    color: "#fff"
  },
  centerAlign: {
    justifyContent: "center",
    position:"absolute",
    bottom:0,
    left:0,

  },
  searchField: {
    width: '80%'
  },
  seachLink: {
    marginTop: '20px'
  },
  cardContentwrap :{
    paddingBottom:"25px"
  }
});





function handleSearch() {

}


export default function AdminCatalog() {
  const classes = useStyles();
  const [productList, setProductList] = useState([]);
  const [showModal, showDeleteModal] = useState(false);
  const [deleteItemId, setDeleteItem] = useState([]);
  const [editFormData, setEditFormData] = useState();
  const [showEdit, setEditModal] = useState(false);

  useEffect(() => {
    fetch('/product/', { method: "GET" }).then(res => {
      console.log("res");
      return res.json();
    }).then(res => {
      setProductList(res);
      return res;
    })
  },
    []);

  useEffect(() => {
    if (editFormData != undefined) {
      setEditModal(true);
    }
  }, [editFormData]);

  function editItem(itemId) {
    fetch(`/product/${itemId}`, { method: "GET" }).then(function (res) {
      return res.json();
    }).then(res => {
      setEditFormData(res);
      return res;
    })

  }

  function deleteItem(pId) {
    showDeleteModal(true);
    setDeleteItem(pId)
  }

  function handleDelete() {
    showDeleteModal(false);
    fetch(`/product/${deleteItemId}`, { method: "DELETE" }).then(function (res) {
      return res.json();
    }).catch(function (e) { console.log(e) });
  }

  function handleClose() {
    showDeleteModal(false);
  }

  function handleEditClose() {
    setEditModal(false);
  }

  function handleChange(evt) {
    const value =
      evt.target.type === "checkbox" ? evt.target.checked : evt.target.value;
    setEditFormData({
      ...editFormData,
      properties: {
       ...editFormData.properties,
        [evt.target.name]: value
      }
    });
  }

  function editSubmit() {
    const requestOptions = {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(editFormData.properties)
    };
    fetch(`/product/update/${editFormData.identity}`, requestOptions).then(function(res){
      return res.json();
    })
  }

  return (
    <React.Fragment>
      <AddProductModal />
      <Grid className={classes.catalogWrapper} container spacing={3}>
        <Grid container item xs={12} sm={12}>
          <Grid container item xs={12} sm={9}>
            <h2>Product Inventory</h2>
          </Grid>
          {/* <Grid container item xs={12} sm={3}>
              <TextField className={classes.searchField} id="standard-basic" label="Search products" />
              <a href="#" onClick={handleSearch} className={classes.seachLink}><SearchIcon/></a>
            </Grid> */}
        </Grid>

        {productList.map(product => (
          <Grid item xs={6} sm={3} key={product.identity}>
            <Card className={classes.root}>
              <CardMedia
                className={classes.media}
                image={product.properties.imgSrc.replace(/[\[\]]/g, '').split(",")[0]}
                title={product.properties.imgAlt}
              />
              <CardContent className={classes.cardContentwrap}>
                <Typography className={classes.productName} gutterBottom variant="h5" component="h2">
                  {product.properties.productName}
                </Typography>
              </CardContent>
              <CardActions className={classes.centerAlign}>
                <Button onClick={() => editItem(product.identity)} size="small" color="primary">
                  Edit
        </Button>
                <Button onClick={() => deleteItem(product.identity)} size="small" color="secondary">
                  Delete
        </Button>
              </CardActions>
            </Card>
          </Grid>
        ))}
      </Grid>
      <DeleteProductModal openModal={showModal} onCloseModal={handleClose} onDelete={handleDelete} />
      <EditProductModal open={showEdit} close={handleEditClose} formData={editFormData} handleChange={handleChange} onUpdate={editSubmit} />
    </React.Fragment>

  );
}
