import React, { Component } from "react";
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card'
import { CardContent } from "@material-ui/core";
import CheckIcon from '@material-ui/icons/Check';
import ComputerIcon from '@material-ui/icons/Computer';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import ImageIcon from '@material-ui/icons/Image';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

const subsData = [
    "20 users included",
    "10GB Data Storage",
    "Help Center Access",
    "Priority Email Support"
]
const useStyles = makeStyles((theme) => ({
   root:{
       backgroundColor:"#f0f3f7",
       minHeight:'550px',
   },
   adminTitle:{
        fontSize:'32px',
        color:'#222',
        marginLeft:'20px'
   },
   dashWrap: {
       justifyContent:'center',
   },
  dashCard: {
       height:'100%',
       margin: '20px',
   },
   List:{
        textAlign:'left',
        display: 'inline-block',
        margin:'10px auto',
        listStyle: 'none',
        fontSize:'15px',
   },
   listItem :{
    padding:"10px"
   },
   navBox: {
       textDecoration:'none',
       display:'flex',
       flexWrap : 'wrap',
       width:'250px',
       justifyContent:'center',
       margin: '10px auto'
   },
   navLink :{
       width: '100px',
       height: '100px',
       margin: '12px',
       textDecoration:'none',
       color:'#fff',
       fontStyle:'bold',
       fontSize:'16px',
       borderRadius:'10px',
       display:'flex',
       flexDirection:'column',
       alignItems:'center',
       justifyContent:'center'
   },
   cardHeader:{
       fontSize:'20px',
       margin:'10px 0'
   },
   iconColor :{
       color:'#05782b'
   }
  }));

export default function AdminDashboard() {
    const classes = useStyles()
    return(
        <Grid className={classes.root}>
            <CssBaseline/>
            <Grid container xs={12} sm={12}>
                <h2 className={classes.adminTitle}> Admin Panel</h2>
            </Grid>
            <Grid className={classes.dashWrap} xs={12} sm={12} container>
            <Grid className={classes.dashCard} item xs={12} sm={5}>
                <Card>
                    <CardContent>
                    <h2 className={classes.cardHeader}>Subscribed Plan - EyeSpy Pro</h2>
                    <List className={classes.List}>
                        {subsData.map(item=>(
                            <ListItem>
                            <ListItemIcon>
                                <CheckIcon className={classes.iconColor}/>
                            </ListItemIcon>
                            <ListItemText primary={item} key={item}/>
                            </ListItem>
                        ))}
                    </List>
                    <div>
                        <Button  variant="contained" color="secondary">Upgrade to Premium</Button>
                    </div> 
                    </CardContent>
                </Card>
                
            </Grid>
            <Grid className={classes.dashCard} item xs={12} sm={5}>
                <Card>
                    <CardContent>
                        <h2 className={classes.cardHeader}>Explore</h2> 
                        <div className={classes.navBox}>
                            <a href="./catalog" style={{backgroundColor:'#ff8a14'}} className={classes.navLink}>
                                <div><ImageIcon/></div>
                                <div>Inventory</div>
                            </a>
                            <a href="./stats" style={{backgroundColor:'#3bd4c9'}} className={classes.navLink}>
                                <div><EqualizerIcon/></div>
                                <div>Statistics</div>
                            </a>
                            <a href="#" style={{backgroundColor:'#e85458'}}  className={classes.navLink}>
                                <div><HelpOutlineIcon/></div>
                                <div>Help</div>
                            </a>
                            <a href="#" style={{backgroundColor:'#9fed74'}}  className={classes.navLink}>
                                <div><ComputerIcon/></div>
                                <div>Demo</div>
                            </a>
                                
                        </div>
                    </CardContent>
                </Card>
            </Grid>
        </Grid>
        </Grid>
        
        
    )
}